import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import NewsFeeds from '../views/NewsFeeds.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/newsfeeds',
    name: 'NewsFeeds',
    component: NewsFeeds
  }
]

const router = new VueRouter({
  routes
})

export default router
